
import XHeader from 'vux/src/components/x-header/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import User from '@/api/user.js'
import Room from '@/api/room.js'
import headImg from '@/assets/image/head.png'
import '@/assets/css/Home.css'

export default {
components:{XHeader,Blur,Grid,GridItem,XDialog,Card,XInput,XNumber,XButton,Box,Divider,Group,Flexbox,FlexboxItem},
data(){
return {
headimg:headImg,
uid:0,
showCreateDialog:false,
showJoinDialog:false,
reward:{
one:0.0,
two:0.0,
three:0.0
},
}
},
methods:{

joinRoom() {
var _this = this;
if (!this.user.uid) {
this.$vux.toast.text('请先登录!'); return;
}
this.$vux.confirm.prompt('', {
'title':'输入房间号',
onConfirm (room_no) {
if (_this.user.room_no && room_no != _this.user.room_no) {

_this.$vux.confirm.show({
title:'提示',
content:'是否退出当前所在房间',
onCancel(){

},
onConfirm(){

Room.outRoom(_this, function(){

_this.user.room_no=0;
_this.$router.push({path:'/room', query:{'room_no':room_no}})
});
}
})
} else {

_this.$router.push({path:'/room', query:{'room_no':room_no}})
}

},
});
},

showCreate() {
var _this = this;
if (!this.user.uid) {
this.$vux.toast.text('请先登录!'); return;
}
if (this.user.room_no) {

this.$vux.confirm.show({
title:'提示',
content:'是否退出当前所在房间',
onCancel(){

},
onConfirm(){

Room.outRoom(_this, function(){

_this.user.room_no=0;
_this.$set(_this, "showCreateDialog", true);
});
}
})
} else {

this.$set(this, "showCreateDialog", true);
}
},

createRoom() {
this.$set(this, "showCreateDialog", false);


var _this = this;
Room.createRoom(this, function(room_no){
_this.user.room_no = room_no;

_this.$router.push({path:'/room', query:{'room_no':room_no}})
})
},

currentRoom() {
console.log('current')
if (!this.user.room_no) {
this.$vux.toast.text('当前没有所在房间');
return;
}
this.$router.push({path:'/room', query:{'room_no':this.user.room_no}})
},

roomList() {
this.$router.push({path:'/room_list'})
},

backClick() {
if (!this.uid) {

location.href="/site/phone"
}
},
test() {
console.log(this.user);
}
},
mounted() {
const _this = this;

User.current(this, function(userinfo){
_this.$set(_this, 'headimg', userinfo.headimg);
_this.$set(_this, 'uid', userinfo.uid);
})
},
computed:{

leftTxt:function() {
return this.uid?this.user.uname:'登录';
}
}
}



